









import { Vue, Component, Prop } from 'vue-property-decorator'

@Component
export default class AppModal extends Vue {

    created() {
        const html = document.getElementsByTagName('html')[0];
        
        html.style.overflow = 'hidden';
    }

    beforeDestroy() {
        const html = document.getElementsByTagName('html')[0];
        
        html.style.overflow = null;
    }
}



