





























































import { Vue, Component, Prop } from "vue-property-decorator";
import Offers from "@/core/offers";
import { OffersSearchData } from '@/../types/app';

const searchDefaults = {
    'destination_id': '',
    'origin_id': '',
    'offer_type': 'airplane',
    'date_from': '',
    'date_to': ''
};

@Component({
    name: 'search-route'
})
export default class SearchRoute extends Vue {

    searchData: OffersSearchData = searchDefaults;

    offers: Offers = new Offers();

    @Prop(String) title: string;

    select(prop, value) {
        this.searchData[prop] = value;
        this.getOffers();
    }

    async getOffers() {
        const offers = new Offers(this.searchData);

        await offers.get();
        this.offers = offers;
    }

    showOffers() {
        // @ts-ignore
        this.$router.push({
            name: 'offers',
            params: {
                offers: this.offers
            }
        });
    }

    clear() {
        // @ts-ignore
        this.$refs.form.clear();
        this.searchData = searchDefaults;
        this.getOffers();
    }

    created() {
        this.getOffers();
    }
}



